  <el-container>
    <el-aside class="sideBar">
      <el-col>
        <h2>Movie DB</h2>
        <el-menu
          default-active="1"
          @select="menuSelect"
          background-color="#3a3a3a"
          text-color="#fff"
          style="border:none"
          active-text-color="#ffd04b"
        >
          <el-menu-item index="1">
            <i class="el-icon-menu"></i>
            <span slot="title">全部</span>
          </el-menu-item>
          <el-menu-item index="2">
            <i class="el-icon-document"></i>
            <span slot="title">电影</span>
          </el-menu-item>
          <el-menu-item index="3">
            <i class="el-icon-document"></i>
            <span slot="title">剧集</span>
          </el-menu-item>
        </el-menu>
      </el-col>
      <el-col>
        <el-tag
          class="filterTag"
          :key="tag"
          v-for="tag in filterTags"
          closable
          :disable-transitions="false"
          @close="closeTag(tag)"
        >
          {{ tag }}
        </el-tag>
        <el-input
          class="inputNewTag"
          v-if="tagInputVisible"
          v-model="tagInputValue"
          ref="saveTagInput"
          size="small"
          @keyup.enter.native="handleTagInputConfirm"
          @blur="handleTagInputConfirm"
        >
        </el-input>
        <el-button
          v-else
          class="button-new-tag"
          size="small"
          @click="showTagInput"
          >+ 输入标签</el-button
        >
      </el-col>
    </el-aside>
    <el-container>
      <el-header>
        <el-col class="optionModule" :span="2" :xs="3">
          <el-popover
            ref="popover"
            placement="right"
            width="200"
            trigger="click"
          >
            <div>
              <div id="topTags" class="hotTag">
                <p>热门标签：</p>
                <el-tag
                  class="movieTag"
                  v-for="tag in hotTags"
                  :key="tag"
                  @click="addTag(tag)"
                  >{{ tag }}
                </el-tag>
              </div>
              <el-tag
                class="filterTag inlineTag"
                :key="tag"
                v-for="tag in filterTags"
                closable
                :disable-transitions="false"
                @close="closeTag(tag)"
              >
                {{ tag }}
              </el-tag>
              <el-input
                class="inputNewTag"
                v-if="tagInputVisible"
                v-model="tagInputValue"
                ref="saveTagInput"
                size="small"
                @keyup.enter.native="handleTagInputConfirm"
                @blur="handleTagInputConfirm"
              >
              </el-input>
              <el-button
                v-else
                class="button-new-tag"
                size="small"
                @click="showTagInput"
                >+ 输入标签</el-button
              >
            </div>
            <el-button
              v-bind:style="optionButtonStyle"
              class="optionButton"
              icon="el-icon-menu"
              circle
              slot="reference"
            ></el-button>
          </el-popover>
        </el-col>
        <el-col :span="4" :xs="10" :sm="6" :md="6" class="searchBar">
          <el-input
            placeholder="输入电影名称"
            v-model="q"
            suffix-icon="el-icon-search"
            clearable
            @input="search"
          ></el-input>
        </el-col>
        <el-col
          :span="5"
          :offset="10"
          :xs="{ span: 10, offset: 0 }"
          :sm="{ span: 8, offset: 6 }"
          :md="{ span: 7, offset: 9 }"
          class="orderMenu"
        >
          <div>
            <el-button
              :class="{ activeText: order_by === '-update_date' }"
              type="text"
              @click="sort('-update_date')"
              >按上新</el-button
            >
            <el-button
              :class="{ activeText: order_by != '-update_date' }"
              type="text"
              @click="sort('-douban_rating')"
              >按评分</el-button
            >
          </div>
        </el-col>
      </el-header>
      <el-main>
        <transition name="el-fade-in-linear">
          <el-row class="movieList" :gutter="20" v-show="movieShow"  element-loading-background="rgba(0, 0, 0, 0.3)" v-loading.fullscreen="loading">
            <el-col
              :span="8"
              :md="6"
              :lg="4"
              :xl="3"
              v-for="movie in movies"
              :key="movie.id"
              class="movieCard"
            >
              <div class="movieItem">
                <el-popover
                  class="pcPoster"
                  :open-delay="400"
                  placement="right"
                  width="400"
                  trigger="hover"
                >
                  <div class="infomer">
                    <div class="intro">
                      {{ movie.intro }}
                    </div>
                    <div>
                      <el-tag
                        class="movieTag"
                        v-for="tag in movie.tags"
                        :key="tag"
                        @click="addTag(tag)"
                        >{{ tag }}</el-tag
                      >
                    </div>
                  </div>
                  <div
                    slot="reference"
                    class="poster"
                    @click="goToMovie(movie.id)"
                    :style="
                      'background-image: url(' + movie.thumbnail_url.replace(/\.webp/,'.jpg') + ')'
                    "
                  ></div>
                </el-popover>
                <div
                  slot="reference"
                  class="poster mobilePoster"
                  @click="goToMovie(movie.id)"
                  :style="'background-image: url(' + movie.thumbnail_url.replace(/\.webp/, '.jpg') + ')'"
                ></div>
              </div>

              <div class="movieInfo">
                <p class="movieTitle" :title="movie.title">
                  {{
                    movie.title +
                    (movie.original_title !== "" ? "&nbsp;/&nbsp;" : "") +
                    movie.original_title
                  }}
                  <span class="movieYear">&nbsp;( {{ movie.year }})</span>
                </p>
                <div class="doubanRating">
                  <label class="豆瓣评分："></label
                  ><el-rate
                    :value="movie.douban_rating / 2"
                    disabled
                    text-color="#ff9900"
                    disabled-void-color=""
                  >
                  </el-rate>
                  <i class="ratingText"> {{ movie.douban_rating }} </i>
                </div>
              </div>
            </el-col>
          </el-row>
        </transition>
      </el-main>
    </el-container>
    <div class="tooltiptext" v-show="webAppTip">
    <img src="data:image/png;base64," class="img-rounded" style="display: inline-block;width: 38px;border-radius: 5px;float: left;">
    <span style="margin-left:10px;display: inline-block;width: 250px;">将此web app安装到本机，点击 <img width="18" height="18" style="display:inline-block;" src="data:image/png;base64,"> 添加到主屏幕</span>
    </div>
  </el-container>
</template>


<script>
// import func from "../../vue-temp/vue-editor-bridge";
const axios = require("axios");
export default {
  name: "home",
  data() {
    return {
      api: process.env.VUE_APP_API,
      token: "",
      loading: false,
      movies: [],
      type: "",
      page: 1,
      limit: 24,
      q: "",
      order_by: "-update_date",
      movieShow: false,
      noMore: false,
      filterTags: [],
      tagInputVisible: false,
      tagInputValue: "",
      hotTags: [],
    };
  },
  computed:{
    //显示PWA提示
    webAppTip: function(){
      if (this.isIos() && !this.isInStandaloneMode()) {
        return true;
      }
      return false;
    },

    optionButtonStyle: function(){
      if(this.filterTags.length >0){
        return {
          color:'tomato',
        }
      } else{
        return {
          color: '#606266',
        }
      }
    }
  },
  methods: {
    // 初始化页面
    init: async function (reset=false) {
      if (localStorage.token && !reset) {
        this.token = localStorage.token;
        axios.defaults.headers.common["Authorization"] = `Bearer ${this.token}`;
        if(this.$route.query.tags) {
          this.filterTags = this.$route.query.tags.split(',');
        }
      } else {
        let result = await this.getToken();
        if (!result){
          let vm = this;
          setTimeout(function () {
            vm.init()
          },600);
        return
        }
      }
      this.getHotTags();
      this.getMovies();
    },
    getToken: async function () {
      let input = await this.$prompt("请输入账号密码（账号:密码）", "认证", {
        confirmButtonText: "确定",
        inputPattern: /.*:.*?/,
        inputErrorMessage: "格式不正确",
      });
      let params = input.value.split(":");
      try {
        let response = await axios.post("/api/auth", {username: params[0],password: params[1],});
        localStorage.token = response.data.access_token;
        this.token = localStorage.token;
        axios.defaults.headers.common["Authorization"] = `Bearer ${this.token}`;
      } catch (err) {
        console.log(err)
        await this.$message({
            type: "info",
            message: "认证失败，请重试",
          });
        return false;
        }
      return true;
    },
    // 监听滚动条
    listenScoller: function () {
      let vm = this;
      window.onscroll = function () {
        if (vm.$route.path!="/home"){
          return;
        }
        let scrollTop = document.documentElement.scrollTop||document.body.scrollTop;
        let windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
        let scrollHeight = document.documentElement.scrollHeight||document.body.scrollHeight;
        if(scrollTop+windowHeight+100>= scrollHeight){
          vm.infLoad();
        }
      };
    },
    // 重置请求参数
    reset: function () {
      this.movieShow = false;
      this.movies = [];
      this.type = "";
      this.page = 1;
      this.limit = 40;
      this.q = "";
      this.order_by = "-update_date";
    },
    // 标签变化刷新页面
    refreshByTag: function () {
      this.noMore = false;
      this.page = 1;
      this.movies = [];
      this.getMovies();
    },
    // 获取电影数据
    getMovies: function () {
      let vm = this;
      vm.loading = true;
      setTimeout(function () {
        axios
          .get(
            `/api/movies?page=${vm.page}&limit=${vm.limit}&q=${
              vm.q
            }&year=&type=${vm.type}&order_by=${
              vm.order_by
            }&tags=${vm.filterTags.join(",")}`
          )
          .then(function (response) {
            if (response.data.length == 0) {
              vm.noMore = true;
            }
            response.data.forEach((movie) => {
              vm.movies.push(movie);
              vm.movieShow = true;
            });
            setTimeout(function () {
              vm.loading = false;
            }, 400);
          })
          .catch(function (error) {
            console.log(error);
            vm.loading = false;
            vm.init(true);
          });
      }, 1000);
    },
    // 菜单选择重新获取数据
    menuSelect: function (index) {
      this.reset();
      if (index === "3") {
        this.type = "剧集";
      } else if (index === "2") {
        this.type = "电影";
      } else {
        this.type = "";
      }
      this.getMovies();
    },
    // 滚动条变化加载新数据
    infLoad: function () {
      if (this.noMore || this.loading) {
        return;
      }
      this.page++;
      this.getMovies();
    },
    // 电影排序
    sort: function (key) {
      this.reset();
      this.order_by = key;
      this.getMovies();
    },
    //电影搜索
    search: function () {
      this.page = 1;
      this.movies = [];
      this.getMovies();
    },
    // 跳转电影对应页面
    goToMovie: function (id) {
      this.$router.push({ path: `/movie/${id}` });
    },
    //新增筛选标签
    addTag: function (tag) {
      if (this.filterTags.indexOf(tag) === -1) {
        this.filterTags.push(tag);
        this.refreshByTag();
      }
    },
    // 去除筛选标签
    closeTag: function (tag) {
      let index = this.filterTags.indexOf(tag);
      this.filterTags.splice(index, 1);
      this.refreshByTag();
    },
    // 显示标签输入框
    showTagInput: function () {
      this.tagInputVisible = true;
      this.$nextTick((_) => {
        console.log(_);
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    // 获取热门标签
    getHotTags: function () {
      let vm = this;
      axios
        .get("/api/tags/top")
        .then(function (response) {
          response.data.forEach(function (tag) {
            vm.hotTags.push(tag[0]);
          });
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    // 输入标签确认
    handleTagInputConfirm: function () {
      let inputValue = this.tagInputValue;
      if (inputValue !== "" && this.filterTags.indexOf(inputValue) === -1) {
        this.filterTags.push(inputValue);
        this.refreshByTag();
      }
      this.tagInputVisible = false;
      this.tagInputValue = "";
    },
    // UA检测
    isIos:function() {
      let userAgent = window.navigator.userAgent.toLowerCase();
      return /iphone|ipad|ipod/.test( userAgent );
    },
    // Standalone检测
    isInStandaloneMode:function(){
      return ('standalone' in window.navigator) && (window.navigator.standalone);
    },

  },
  mounted() {
    this.init();
    this.listenScoller();
  },
  watch: {
    //监听路由
      $route(){
        if (this.$route.path==="/home" &&localStorage.newTag && localStorage.newTag!=""){
          //新标签筛选
          if(this.filterTags.indexOf(localStorage.newTag) === -1) {
            this.filterTags.push(localStorage.newTag)
            this.refreshByTag();
            localStorage.newTag = "";
          }
        }
      },
  },

};
</script>

