<script type="application/javascript" ></script>
<script>
const axios = require("axios");
import postscribe from "postscribe";
export default {
  name: "movie",
  data: function () {
    return {
      window: window,
      id: "",
      playOptions: [
        { label: "nPlayer" },
        { label: "VLC" },
        { label: "MXPlayer" },
        { label: "MXPlayer Pro" },
        { label: "PotPlayer" },
        { label: "IINA" },
      ],
      fanart: "",
      playType: "nPlayer",
      videoFiles: [],
      user:'',
      password:'',
      movieInfo: {},
      introFull: false,
      potplayer: `C:\\\\Program Files\\\\DAUM\\\\PotPlayer\\\\PotPlayerMini64.exe`,
      tipVisible: false,
      fromIndex: false,
      contentShow: false,
      playListVisible: false,
      relatedMovies:[],
      relatedMoviesLoading: true,
    };
  },
  computed: {
    //播放选项列表
    playList: function () {
      let playList = [];
      for (let video of this.videoFiles) {
        if (this.playType === "nPlayer") {
          playList.push({
            link: `nplayer-${document.location.protocol}//${this.user}:${this.password}@${document.location.host}/share${video}`,
          });
        }
        if (this.playType === "MXPlayer") {
          playList.push({
            link: `intent:${document.location.protocol}//${this.user}:${this.password}@${document.location.host}/share${video}#Intent;package=com.mxtech.videoplayer.ad;S.title=New%20title;end`,
          });
        }
        if (this.playType === "MXPlayer Pro") {
          playList.push({
            link: `intent:${document.location.protocol}//${this.user}:${this.password}@${document.location.host}/share${video}#Intent;package=com.mxtech.videoplayer.pro;S.title=New%20title;end`,
          });
        }
        if (this.playType === "VLC") {
          playList.push({
            link: `vlc://${document.location.protocol}//${this.user}:${this.password}@${document.location.host}/share${video}`,
          });
        }
        if (this.playType === "PotPlayer") {
          playList.push({
            link: `potplayer://${document.location.protocol}//${this.user}:${this.password}@${document.location.host}/share${video}`,
          });
        }
        if (this.playType === "IINA") {
          playList.push({
            link: `iina://weblink?url=${document.location.protocol}//${this.user}:${this.password}@${document.location.host}/share${video}`,
          });
        }
      }
      return playList;
    },
    //potplayer注册协议代码
    pcRegCode: function () {
      return `Windows Registry Editor Version 5.00

[HKEY_CLASSES_ROOT\\potplayer]
@="\\"URL:potplayer protocol\\""
"URL Protocol"=""

[HKEY_CLASSES_ROOT\\potplayer\\shell]

[HKEY_CLASSES_ROOT\\potplayer\\shell\\open]

[HKEY_CLASSES_ROOT\\potplayer\\shell\\open\\command]
@="cmd /k ( set \\"var=%1\\" & call set var=%%var:potplayer://=%% & call \\"${this.potplayer}\\" %%var%%)"
`;
    },
    // 完整标题
    fullTitle: function () {
      if (this.movieInfo.originalTitle !== "") {
        return `${this.movieInfo.title} / ${this.movieInfo.originalTitle}（${this.movieInfo.year}）`;
      }
      return `${this.movieInfo.title}（${this.movieInfo.year}）`;
    },
    introClass: function () {
      return this.introFull
        ? "overview-intro-full overview-intro detailItem"
        : "overview-intro detailItem";
    },
  },
  methods: {
    //初始化页面
    init: function () {
      //根据UA显示默认播放器
      if (!this.isPC()) {
        this.playType = "VLC";
      } else {
        this.playType = "PotPlayer";
        if (!localStorage.pc_tip) {
          this.tipVisible = true;
          localStorage.pc_tip = 1;
        }
      }
      if (localStorage.play_type) {
        this.playType = localStorage.play_type;
      }
      // 从路由获取ID
      this.id = this.$route.params.id;
      // 判断是否授权
      if (localStorage.token) {
        this.token = localStorage.token;
        axios.defaults.headers.common["Authorization"] = `Bearer ${this.token}`;
      } else {
        window.open('/','_self')
      }
    },
    // 判断UA是否为PC
    isPC: function () {
      let flag = navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone|Mac)/i
      );
      return !flag;
    },
    //修改播放选项
    changePlayType: function (value) {
      localStorage.play_type = value;
    },
    //关闭播放列表
    closePlayList: function () {
      let currentCli = document.getElementsByClassName("playLinks")[0];
      if (currentCli) {
        if (!currentCli.contains(event.target)) {
          //点击到播放链接以外的区域隐藏
          this.playListVisible = false;
        }
      }
    },
    //获取电影数据
    getData: function () {
      let vm = this;
      axios
        .get(`/api/movie/${vm.id}`)
        .then(function (response) {
          let data = response.data;
          vm.poster = data.thumbnail_url;
          vm.videoFiles = data.play_links;
          vm.user = data.user
          vm.password = data.password
          vm.fanart = data.fanart
            ? "https://www.themoviedb.org/t/p/original" + data.fanart
            : "/7d7d7d.png";
          vm.movieInfo = data;
          vm.contentShow = true;
          // 获取相似电影
          vm.getRelatedMovies();
        })
        .catch(function (error) {
          console.log(error);
          window.open('/','_self')
        });
    },
    //全排列
    getGroup: function(data, index = 0, group = []) {
      var need_apply = new Array();
      need_apply.push(data[index]);
      for(var i = 0; i < group.length; i++) {
        need_apply.push(group[i]+','+data[index]);
      }
      group.push.apply(group, need_apply);
    
      if(index + 1 >= data.length) return group;
      else return this.getGroup(data, index + 1, group);
    },
    //获取相似电影
    getRelatedMovies: async function(){
      let tags = []
      for (let i=0;i<this.movieInfo.tags.length;i++){
        tags.push(this.movieInfo.tags[i].text)
      }
      let groupTags = this.getGroup(tags);
      let sortedGroupTags = groupTags.map(i => ({raw: i, len: i.length}))
                .sort((p, n) => n.len - p.len)
                .map(i => i.raw);
      
      let relatedMovies = []
      // 遍历全排列TAG列表
      for (let i=0;i<sortedGroupTags.length;i++){
        // 达到数量停止
        if (relatedMovies.length >=5 || i>=30){
          break;
        }
        let response = await axios.get(`/api/movies?page=1&limit=10&q=&year=&type=&order_by=&tags=${sortedGroupTags[i]}`)
        // 遍历请求结果
        for (let j=0;j<response.data.length;j++){
          // 相关电影列表ID
          let existsIds = [parseInt(this.id)];
          for (let k=0;k<relatedMovies.length;k++){
            existsIds.push(relatedMovies[k].id);
          }
          // 如果结果不在列表中，加入列表
          if (existsIds.indexOf(response.data[j].id) ==-1){
            relatedMovies.push(response.data[j]);
          }
        }
      }
      this.relatedMovies = relatedMovies.slice(0,8);
      this.relatedMoviesLoading = false;
    },
    // 返回主页
    goBack: function () {
      if (this.fromIndex) {
        this.$router.go(-1);
      } else {
        this.$router.push({ path: "/home" });
      }
    },
    gotoMovie:function(mid) {
      this.$router.push({ path: `/movie/${mid}` });
    },
    // 标签筛选
    addTag: function (tag) {
      localStorage.newTag = tag;
      this.goBack();
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (from.fullPath === "/home") {
        vm.fromIndex = true;
      } 
    });
  },
  mounted: function () {
    this.init();
    this.getData();
    
  },
};
</script>

